<template>
  <div class="components-container main-container">
    
    <div class="tips">
      <el-link class="font" type="primary" href="https://quilljs.com/docs/configuration/" target="_blank">quill</el-link>
       CDN npm 
      <p>1</p>
      <p>2</p>
      <p>3</p>
    </div>

    <h2></h2>
    
    <el-upload
      id="img-upload"
      action="https://httpbin.org/post"
      :multiple="false"
      :show-file-list="false"
      :on-success="richUploadSuccess"
      style="height: 10px;">
    </el-upload>
    <div id="quill-editor" ref="quill-editor"></div>
    <!-- quill -->
    <div class="quill-count">
      <span class="number">{{richCurrentLength}}/{{richMaxLength}}</span>
    </div>

    <el-divider content-position="left"></el-divider>

    <div class="ql-editor" v-html="content"></div>

    <el-divider content-position="center">End</el-divider>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import Quill from 'quill'
import ImageResize from 'quill-image-resize-module'
import { ImageDrop } from 'quill-image-drop-module'

Quill.register('modules/imageResize', ImageResize)
Quill.register('modules/imageDrop', ImageDrop)

export default {
  // 
  toolbarOptions: [
    [{ 'size': ['small', false, 'large', 'huge'] }], // 
    [{ 'header': [1, 2, 3, 4, 5, 6, false] }], // 
    ['bold', 'italic', 'underline', 'strike'], // 
    [{ 'indent': '-1' }, { 'indent': '+1' }], // 
    [{ 'color': [] }, { 'background': [] }], // 
    [{ 'align': [] }], // 
    ['clean'], // 
    ['image'],
    ['custom'] // 
  ],
  // 
  imageFunction(val) {
    if(val) {
      document.querySelector('#img-upload input').click()
    } else {
      this.quill.format('image', false)
    }
  },
  data() {
    return {
      // 
      content: '',
      richMaxLength: 800,
      richCurrentLength: 0
    }
  },
  watch: {
    content() {
      // 
      this.richCurrentLength = this.quill.getLength() - 1
      let numWrapper = document.querySelector('.quill-count')
      if(this.richCurrentLength > this.richMaxLength) {
        numWrapper.style.color = 'red'
      } else {
        numWrapper.style.color = '#666'
      }
    }
  },
  methods: {
    // 
    richUploadSuccess(response, file, fileList) {
      /**
       * 
       * ps
       * 
       * 
       *  imgUrl
       */
      if(response.files.file) {
        let imgUrl = response.files.file
        // 
        let length = this.quill.getSelection().index
        // res
        this.quill.insertEmbed(length, 'image', imgUrl)
        // 
        this.quill.setSelection(length + 1)
      } else {
        // Message
        this.$message.error('')
      }
    },
    onEditorChange(eventName, ...args) {
      if(eventName === 'text-change') {
        // args[0] will be delta
        // 
        this.content = document.querySelector('#quill-editor').children[0].innerHTML
      } else if (eventName === 'selection-change') {
        // args[0] will be old range
      }
    },
    // quill
    // quilltoolbar
    initCustomQullToolbar() {
      const timeButton = document.querySelector('.ql-custom')
      timeButton.style.cssText = 'width: 80px; outline: none;'
      timeButton.innerText = ''
    },
    // 
    quillCustomFunction() {
      const h = this.$createElement
      this.$notify({
        type: 'success',
        title: 'quill',
        message: h('i', {style: 'color: teal'}, ' Quill  Quill ')
      })
    },
    initQuill() {
      const quill = new Quill('#quill-editor', {
        // 
        theme: 'snow',
        placeholder: 'Compose an epic...',
        debug: 'error',
        modules: {
          toolbar: {
            container: this.$options.toolbarOptions,
            handlers: { // 
              'image': this.$options.imageFunction,
              'custom': this.quillCustomFunction
            }
          },
          imageDrop: true,
          imageResize: {
            modules: ['Resize', 'DisplaySize', 'Toolbar'],
            handleStyles: {
              backgroundColor: 'black',
              border: 'none',
              color: 'white'
            },
            displayStyles: {
              backgroundColor: 'black',
              border: 'none',
              color: 'white'
            },
            toolbarStyles: {
              backgroundColor: 'black',
              border: 'none',
              color: 'white'
            },
            toolbarButtonStyles: {},
            toolbarButtonSvgStyles: {}
          }
        }
      })
      this.quill = quill
      // quilltoolbar
      this.initCustomQullToolbar()
      /**
       * 
       * editor-change  text-changeselection-change
       *  text-change  selection-change
       * https://quilljs.com/docs/api/#text-change
       */
      quill.on('editor-change', this.onEditorChange)
    }
  },
  mounted() {
    this.initQuill()
  },
  beforeDestroy() {
    this.quill = null
    delete this.quill
  }
}
</script>

<style scoped>
.main-container {
  max-width: 80%;
  min-width: 800px;
  margin-left: auto;
  margin-right: auto;
}
.tips {
  width: 600px;
  padding: 15px 20px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  line-height: 2;
}
.font {
  font-size: 18px;
}
#quill-editor {
  /*width: 80%;*/
  height: 300px;
}
/**/
.quill-count {
  border: 1px solid #ccc;
  border-top: none;
  height: 30px;
  line-height: 30px;
  text-align: right;
  padding-right: 10px;
  font-size: 14px;
  color: #666;
}
/*  */
.ql-editor {
  margin-bottom: 50px;
}
</style>



